
.summary-item {
    margin-bottom: 10px;
  }
  
    .step-container {
      display: flex;
      flex-wrap: wrap;
    }
    
.step-container {
  margin: 10%;
}
  
    .summary-item {
      flex-basis: 48%;
      margin-right: 2%;
    }
  
    .button-container {
      flex-basis: 100%;
      max-width: 500px;
    }
  



.summary-list {
  list-style-type: none;
  padding: 0;
}

.summary-item {
  margin-bottom: 15px;
}

.summary-label {
  display: inline-block;
  min-width: 150px;
  font-weight: bold;
}

.button-container {
  margin-top: 20px;
}

.button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-right: 10px;
}

.button:hover {
  background-color: #0056b3;
}

.button:focus {
  outline: none;
}

.button:disabled {
  background-color: #d1d1d1;
  cursor: not-allowed;
}


.summary-table {
  font-size: 20px;
  border-radius: 20px;
}

.summary-label {
  margin: 5px;
}


@media (min-width: 1000px) {
  .summary-table {
    font-size: 20px;
  }

  .summary-label {
    margin: 10px;
  }


}


/* Add this to your Step4.css file */
.green-button {
  background-color: green;
}
