.email-phone-form{ 
    margin: 0;
    font-weight: bold;
    margin-right: 50px;
    width: 500px;
    font-size: 20px;
    margin-bottom: 20px;
  }

  .progress-text-step3{
    color: white;
    padding-left: 150px;
    padding-top: 1px;
    font-size: 12px;
  }

  .progress-bar-step3 {
    width: 350px;
    height: 20px;
    background-color: #f0f0f0; /* Grey background color */
    border-radius: 10px;
  }

  .email-phone-text-form{
    padding-bottom: 30px;
    font-weight: bold;
    font-size: 14px;
  }
  
  
@media screen and (min-width: 768px) {
    .email-phone-form{
        margin: 0;
        font-weight: bold;
        margin-right: 50px;
        width: 500px;
        font-size: 20px;
        margin-bottom: 20px;
      }

      .progress-text-step3{
        color: white;
        padding-left: 170px;
        padding-top: 2.5px;
        font-size: 12px;
      }

      .progress-bar-step3 {
        width: 350px;
        height: 20px;
        background-color: #f0f0f0; /* Grey background color */
        border-radius: 10px;
      }

      .email-phone-text-form{
        padding-bottom: 30px;
        font-weight: bold;
      }     
}

